import { makeStyles } from '@mui/styles';

export const styles = makeStyles({
  root: {
    display: 'flex',
    flexDirection: 'column',
    position: 'absolute',
    bottom: '-60vh',
    right: 0,
  },
  shell: {
    height: '60vh',
    userSelect: 'none',
    pointerEvents: 'none',
    '& > img': {
      height: '100%',
    },
    zIndex: 4,
  },
  notifications: {
    zIndex: 2,
  },
  appWrapper: {
    display: 'flex',
    flexDirection: 'column',
    position: 'absolute',
    top: '1vh',
    left: '.4vh',
    height: 'calc(100% - 2.3vh)',
    width: 'calc(100% - .8vh)',
    backgroundSize: 'cover!important',
    zIndex: 1,
  },
  activeApp: {
    height: '100%',
    width: '100%',
    overflow: 'auto',
    position: 'relative',
  },
});
