import React, { useEffect } from 'react';
import { useSelector } from 'react-redux';
import { Alert, Snackbar } from '@mui/material';
import CssBaseline from '@mui/material/CssBaseline';
import { StyledEngineProvider, ThemeProvider } from '@mui/material/styles';

import { nuiAction } from './lib/nui-comms';
import { store, type } from './lib/redux';
import { theme } from './base.styles';

export const IndexProvider = ({ children }) => {
  const isError = useSelector<RootState, null | Error>(state => state.main.error);
  const isMounted = useSelector<RootState, boolean>(state => state.main.mounted);

  const handleClose = () => {
    nuiAction('reload');
  };

  useEffect(() => {
    store.dispatch({
      type,
      cb: state => ({
        ...state,
        main: {
          ...state.main,
          mounted: isError === null || isError.message.trim() === '',
        },
      }),
    });
  }, [isError]);

  return (
    <StyledEngineProvider injectFirst>
      <ThemeProvider theme={theme}>
        <CssBaseline />
        <Snackbar
          open={isError !== null && isError?.message.trim() !== ''}
          autoHideDuration={5000}
          onClose={handleClose}
        >
          <Alert onClose={handleClose} severity='error' sx={{ width: '100%' }}>
            An error occurred in ${isError}. Reloading the UI...
          </Alert>
        </Snackbar>
        {isMounted && children}
      </ThemeProvider>
    </StyledEngineProvider>
  );
};
