import React, { FC, useMemo, useState } from 'react';

import { checkImageValidity, extractLinks } from '../lib/util';
import { styles } from '../styles/components/textwrapper.styles';

import { Imagecontainer } from './imagecontainer';

declare interface ExtractedImage {
  isLoading: boolean;
  src: string;
}

export const Textwrapper: FC<{ children: string }> = props => {
  const classes = styles();
  const [images, setImages] = useState<ExtractedImage[]>([]);

  const strippedText = useMemo((): string => {
    // eslint-disable-next-line prefer-const
    let { text, links } = extractLinks(props.children);
    new Promise<void>(res => {
      let _images: ExtractedImage[] = [];
      // Need 2 loops otherwise it will not show when loading
      links.forEach(async l => {
        _images.push({
          isLoading: true,
          src: l,
        });
      });
      setImages(_images);
      links.forEach(async l => {
        const valid = await checkImageValidity(l);
        if (valid) {
          const idx = _images.findIndex(p => p.src === l);
          if (idx < 0) {
            return;
          }
          _images[idx].isLoading = false;
          setImages(_images);
          return;
        }
        // When the image is not valid
        _images = _images.filter(p => p.src !== l);
        // Check for string position of l in text
        const txtIdx = String(props.children).indexOf(l);
        if (txtIdx > -1) {
          // Put l at txtIdx position in string
          text = text.slice(0, txtIdx) + l + text.slice(txtIdx);
        }
        setImages(_images);
      });
      res();
    });
    return text;
  }, [props.children]);

  return (
    <div className={classes.wrapper} style={images.length > 0 ? { minWidth: '21vh' } : {}}>
      <span>{strippedText}</span>
      <div className={classes.imageList}>
        {images.map((img, idx) => (
          <Imagecontainer url={img.src} loading={img.isLoading} key={idx} />
        ))}
      </div>
    </div>
  );
};
